body{
    background-color: rgb(201, 201, 201);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    color: rgb(26, 47, 95);
  }
#add{
    border-radius: 15px;
    background-color: rgb(56, 123, 217);
    color: white;
    font-size: large;
    font-weight: bold;
    padding: 5px 12px;
}
#add:hover{
    background-color: rgb(28, 14, 93);
    padding: 8px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 10px;
    transition: 0.5s;
}
